import React, { forwardRef } from "react";
export default forwardRef(({ props }, ref) => (
  <main ref={ref} className="about">
    <h3 className="heading">My Biography</h3>
    <section>
      <p>
        I'm a web developer with over 2 years of experience doing work on HTML,
        CSS and JavaScript. I love spending my time with new technologies and
        React.js stuff. I mainly work with React.js and Next.js.
      </p>
    </section>
  </main>
));
